import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import { ButtonGroup } from '@material-ui/core';

const useStyles = makeStyles({
  root: {
    minWidth: '150px',
  }
});

const NoticeDeleteDialog = props => {
  const classes = useStyles();
  const { onDelete, onClose, value, open } = props;

  const handleDelete = (i, j) => () => {
    onDelete(i,j);
    onClose();
  }

  const handleClose = () => {
    onClose();
  };

  return (
    <Dialog onClose={handleClose} aria-labelledby="delete-dialog-title" open={open} className={classes.root}>
      <DialogTitle id="delete-dialog">  ?</DialogTitle>
      <ButtonGroup
        orientation="vertical"
        color="primary"
        aria-label="vertical contained primary button group"
        variant="text"
      >
        <Button onClick={handleDelete(value)}></Button>
        <Button onClick={handleDelete(value, true)}> </Button>
        <Button onClick={onClose}></Button>
      </ButtonGroup>
    </Dialog>
  );
}

export default NoticeDeleteDialog;